.project-image-div {
  position: relative;
  width: 770px;
  height: 390px;
  margin-left: 40%;
  margin-top: 5%;
}

.project-image {
  border-radius: 10px;
}

.caption {
  color: white;
  font-size: 21px;
}

/*Ipad Pro*/
@media screen and (max-width: 1024px) and (max-height: 1366px)  {
  .project-image-div {
    position: relative;
    width: 250px;
    height: 390px;
    margin-left: 40%;
    margin-top: 5%;
  }
}