}

.slide-button-left {
  transform: scale(1.4);
  background: none;
  border: 0px;
  color: white;
  margin-left: 8.5px;
}

.slide-button-right {
  transform: scale(1.4);
  background: none;
  border: 0px;
  color: white;
}

